@import url(https://fonts.googleapis.com/css?family=PT+Sans+Caption&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  margin: 0;
  padding: 0;
  background: #fefefe;
  color:#39364f;
  height: 100%;
}
*{
  box-sizing: border-box;
}
h1,h2,h3,h4,h5,p{
  margin: 0;
  padding: 0;
}

.container{
  max-width: 1000px;
  margin: 0 auto;
}

.header{
    display: flex;
    justify-content: space-between;
}
.logoContainer a{
    text-decoration: none;
    color:#f05537
}
.viewMore{
    display: block;
    
    background: #fff;
    color:#222;
    width: 400px;
    border: 1px solid #ddd;
    padding: 5px 30px;
    margin: 0 auto;
    margin-top: 20px;
}
.eventContent h1{
    font-size: 20px;
    margin: 10px 0;

}
.eventContent p{
    line-height: 23px;
}
.eventContent{
    background: #fff;
    margin-top: 10px;
    padding: 20px;
}
.MainHeader{
    padding: 20px 0;
    box-shadow: 0 5px 10px #ddd;
}
.logoContainer{
    align-items: center;
}
.Menu{
    justify-content: right;    
}
.Menu li{
    display: inline-block
}
.Menu li a{
    padding: 10px 20px;
    text-decoration: none;
    color:#6f7287
}
.EventsPages h1{
padding: 20px 0;
/* font-family: 'PT Sans Caption', sans-serif; */
font-size: 25px;
font-weight: bold;

}
.active{
    color: #f05537 !important;

}
.MainColor{
    color: #f05537;
}

.danger{
    background: red;
    color:#fff;
}
.sucess{
    background: green;
    color:#fff
}
.mainCover{
    width: 100%;
    height: 300px;
    object-fit: cover;
}
.searchBy{
    margin: 20px 0;
    padding: 0;
    
}
.searchBy li{
    list-style-type: none;
    display: inline;
}
.searchBy li a{
    margin: 0 20px;
    text-decoration: none;
    color:#39364f;
    font-weight: bold;
}

.RegistrationFrom{
    max-width: 300px;
    margin: 0 auto;
    -moz-height: calc(100%-100px);
}
.grid-2{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
}
.RegistrationFrom h1{
    margin: 5px 0;
    text-align: center;   
}
.RegistrationFrom i{
    font-size: 50px;
    display: block;
    text-align: center;
}
.RegistrationFrom p{
    text-align: center;
    padding:10px
}
.RegistrationFrom input,textarea{
    width: 100%;
    padding: 10px;
    margin: 5px;
    border: 1px solid #ddd;
    border-radius: 5px;
    /* font-size: 20px; */
}
.RegistrationFrom input[type="submit"]{
    background:#f05537;
    color:#fff;

}
.RegistrationFrom input::-webkit-input-placeholder{
    font-size: 15px;
}
.RegistrationFrom input::-moz-placeholder{
    font-size: 15px;
}
.RegistrationFrom input:-ms-input-placeholder{
    font-size: 15px;
}
.RegistrationFrom input::-ms-input-placeholder{
    font-size: 15px;
}
.RegistrationFrom input::placeholder{
    font-size: 15px;
}

 .EventCardContent{
    background: #fff;
    box-shadow: 0 0 10px #ddd;
}
.registrationFess{
    color: #f05537;
    background:#fff;
    position: absolute;
    right:0px;
    top: 10px;
    /* box-shadow: 0 0 2px #6c6c6c; */
    padding: 5px 20px;
    font-weight: bold;
}
.Eventlocation{
    color:#757171;
    padding-left: 5px;
}
.card-content i{
    color:#757171;
}
.listOfAllevents{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
}
.listOfAllevents a {
    text-decoration: none;
}
.listOfAllevents a:hover{
    box-shadow: 0 0 5px #23212d;
}

.EventTime{
    color:#f05537;
}
footer{
    display: block;
    background: #23212d;
    color:#fff;
    text-align: left;
    padding: 20px 10px;
    margin-top: 20px;
    width: 100%;  
}
.searchBar {
    position: relative;
}
.searchBar i {
    position: absolute;
    top:10px;
    left:10px;
}
.searchBar input{
    width: 350px;
    padding: 5px 30px;
    border-radius: 5px;
    border:1px solid #ddd;
}
.card-title{
    display: block;
    font-family: 'PT Sans Caption', sans-serif;
    color:#39364f;
    margin-bottom:10px;
    font-size: 20px;
    font-weight: bold;
    height: 55px;
    margin: 5px 0;
    width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
}
.EventImage img{
    width: 100%;
    height: 200px;
    object-fit: cover;

}
.card-content{
    padding: 20px;
}
.EventImage{
    position: relative;
}
.eventBannerContent{
    box-shadow: 0 0 5px #ddd;;
}
.eventInfo{
    display: flex;
    justify-content: space-between;
    background: #fff;
    color:#39364f;
    padding: 20px 20px;
    box-shadow:  0 0 10px #ddd;
    text-align: center;
}
.eventBannerContent  img{
    width: 100%;
}
.otherEvents{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 10px;
}
.registerForm{
    width: 350px;
    padding: 20px;
}
.registerForm h4{
    margin: 5px 0;
}
.registerForm input{
    width: 100%;
    margin: 5px;
    padding: 5px;
}
.eventInfo i{
    display: block;
    font-size: 25px;
    padding-bottom: 10px;
    text-align: center;
    margin: 0 auto;
}
.eventInfoTitile{
    font-weight: bold;
    padding-bottom: 5px;
}
.register{
    display: flex;
    align-items: center;
}
.registerBtn{
    background: #f05537;
    color: #fff;
    font-size: 20px;
    padding: 5px 20px;
    border: none;
    font-weight: bold;
    align-items: center;
}

.header{
    /* box-shadow: 0 0 10px #ddd; */
    padding: 10px 0;
}
.headerGrid{
    display: grid;
    grid-template-columns: 5fr 1fr;
}
.createEventForm{
    max-width: 600px;
    margin: 0 auto;
}
.createEventForm h3{
    padding: 20px;
    text-align: center;
}
.createEventForm input,.createEventForm textarea, select{
    display: block;
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ddd;   
    margin-bottom: 10px;
    

}

.logoContainer h1{
    color:#f05537;
    font-size: 30px;
    font-weight: bold;
}
.logoContainer{
    display: flex;
    align-items: center;
}
.Menu ul{
    display: flex;
    justify-content: space-between;
}
.Menu ul li a{
    font-size: 18px;
    color:#222;
}
.Menu ul li a:hover{
    color:#f68220;
}

.dahsbordGrid{
    display: grid;
    grid-template-columns: 300px 1fr;
}
.DashboardMenu a{
    display: block;
    font-size: 20px;
    color:#f05537;
    padding: 5px 10px;
}
.DashboardMenu{
    background:#f7f4f4;
    padding: 30px 0;
    height: 100vh;
}
.noEvent{
    font-size: 15px;
    text-align: center;
    margin: 30px;
}
